.Container {
    height: 400px;
    margin: 0 auto;
    margin-top: 220px;
    color: white;
    background: -webkit-linear-gradient(15deg, rgba(0, 0, 0, 0.39), rgba(255, 255, 255, 0.438))no-repeat fixed;
    background: -o-linear-gradient(15deg, rgba(0, 0, 0, 0.39), rgba(255, 255, 255, 0.438))no-repeat fixed;
    background: linear-gradient(75deg, rgba(0, 0, 0, 0.39), rgba(255, 255, 255, 0.438))no-repeat fixed;
}

.UsersList > tbody > tr:nth-child(even){
    background-color: rgba(255, 255, 255, 0.459);
    /* display: table; */
}
    
    

.UsersList > tbody > tr {
    width: 598px;
    padding-top: 10px;
}

.UsersList > tbody > td {
    padding: 5px;
}

.UsersList > tbody > tr:hover {
    background-color: #b6edd02d;
}


